.social__list svg {
  width: 30px;
  height: 30px; }

.accordion {
  display: flex;
  justify-content: space-between;
  width: 70%; }
  @media (max-width: 1023px) {
    .accordion {
      width: 100%; } }
  @media (max-width: 767px) {
    .accordion {
      display: block;
      margin: 0 auto;
      border-top: 1px solid rgba(248, 253, 255, 0.7); } }

@media (max-width: 767px) {
  .accordion__item {
    padding: 0 25px;
    border-bottom: 1px solid rgba(248, 253, 255, 0.7); } }

@media (max-width: 767px) {
  .accordion__title {
    display: flex;
    justify-content: space-between;
    padding-top: 11px;
    padding-bottom: 9px; } }

.accordion__title h3 {
  max-width: 60%;
  margin: 0;
  margin-bottom: 19px;
  padding: 0;
  padding-top: 7px;
  font-weight: 400;
  font-size: 15px;
  line-height: 16px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff; }
  @media (max-width: 1023px) {
    .accordion__title h3 {
      margin-bottom: 17px; } }
  @media (max-width: 767px) {
    .accordion__title h3 {
      margin-bottom: 0;
      padding-top: 10px;
      padding-bottom: 13px;
      font-size: 15px;
      line-height: 16px; } }

.accordion__content ul {
  margin: 0;
  padding: 0;
  list-style: none; }

.accordion__content a {
  display: block;
  margin-right: 30px;
  padding: 5px 0 3px;
  color: #ffffff;
  opacity: 0.7;
  text-decoration: none;
  cursor: pointer; }
  .accordion__content a:hover, .accordion__content a:focus {
    color: #00b2ff;
    opacity: 1; }
  .accordion__content a:active {
    opacity: 0.5; }
  @media (max-width: 1023px) {
    .accordion__content a {
      margin-right: 10px; } }
  @media (max-width: 767px) {
    .accordion__content a {
      margin-bottom: 12px;
      margin-right: 0;
      font-size: 14px;
      line-height: 19px; } }

@media (max-width: 767px) {
  .accordion__content a[href^="tel:"] {
    margin-bottom: 3px; } }

.accordion__item--navigation {
  padding-left: 9px; }
  @media (max-width: 1023px) {
    .accordion__item--navigation {
      padding-left: 0; } }

.accordion__item--navigation .accordion__content {
  display: flex; }
  @media (max-width: 1023px) {
    .accordion__item--navigation .accordion__content {
      display: block; } }
  @media (max-width: 767px) {
    .accordion__item--navigation .accordion__content {
      display: none; } }

.accordion__item--navigation a {
  max-width: 220px; }

.accordion__item--contacts {
  max-width: 345px; }
  @media (max-width: 1023px) {
    .accordion__item--contacts {
      min-width: 180px;
      padding-left: 25px; } }
  @media (max-width: 767px) {
    .accordion__item--contacts {
      max-width: 100%; } }

.accordion__toggle {
  display: none; }
  @media (max-width: 767px) {
    .accordion__toggle {
      display: block;
      margin: 0;
      margin-top: -3px;
      margin-right: 1px;
      padding: 0;
      padding-left: 10px;
      background-color: transparent;
      border: none; } }

.accordion__toggle[data-nojs="no-js"] {
  display: none; }

@media (max-width: 767px) {
  .accordion__toggle:hover svg,
  .accordion__toggle:focus svg {
    fill: #00b2ff;
    opacity: 1; } }

.accordion__toggle:active svg {
  opacity: 0.5; }

.accordion__toggle svg {
  fill: #f8fdff; }

@media (max-width: 767px) {
  .accordion__item .accordion__content {
    display: none; } }

@media (max-width: 767px) {
  .accordion__item[data-state="is-open"] .accordion__content {
    display: block;
    padding-bottom: 11px; } }

@media (max-width: 767px) {
  .accordion__item[data-state="is-open"] .accordion__icon--close {
    display: none; } }

@media (max-width: 767px) {
  .accordion__item[data-state="is-open"] .accordion__title {
    padding-bottom: 0; } }

